import {Component, EventEmitter, Input, Output} from '@angular/core';
import {NbToastrService} from '@nebular/theme';
import {DialogService} from '../dialog/dialog.service';
import {ServiceDataSource} from '../../../utils/service-data-source';
import {deepClone, fillMissingPropertiesClones} from '../../../utils/utils';
import {FieldDefinitionViewModel} from '../../../generated-api/models';

@Component({
  selector: 'ngx-smart-table',
  templateUrl: './smart-table.component.html',
  styleUrls: ['./smart-table.component.css']
})
export class SmartTableComponent<T> {

  private static readonly defaultSettings = {
    actions: {
      add: false,
      position: 'right',
    },
    extraActions: {
      add: true,
      search: true,
    },
    hideSubHeader: true,
    add: {
      addButtonContent: '<i class="nb-plus"></i>',
      createButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
      confirmCreate: true,
    },
    edit: {
      editButtonContent: '<i class="nb-edit"></i>',
      saveButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
      confirmSave: true,
    },
    delete: {
      deleteButtonContent: '<i class="nb-trash"></i>',
      confirmDelete: true,
    }
  };

  allSettings: any = SmartTableComponent.defaultSettings;
  tableVisible = true;
  fieldsDefinitions: FieldDefinitionViewModel[];

  @Input()
  dataSource: ServiceDataSource<T>;

  @Input()
  validator: (value: T) => Promise<T>;

  @Output()
  custom = new EventEmitter();

  constructor(
    private readonly toastrService: NbToastrService,
    private readonly dialogService: DialogService
  ) {
  }

  @Input()
  set settings(settings: any) {
    if (this.allSettings == null) {
      this.allSettings = fillMissingPropertiesClones(settings, SmartTableComponent.defaultSettings);
    } else {
      this.allSettings = fillMissingPropertiesClones(settings, this.allSettings);
    }

    this.refresh();
  }

  @Input()
  set fieldsModel(fieldsModel: FieldDefinitionViewModel[]) {
    this.fieldsDefinitions = fieldsModel;
    if (fieldsModel != null) {
      const settings: any = {
        columns: {},
      };
      fieldsModel.filter(x => x.display).forEach(field => {
        settings.columns[field.parameterName] = {
          title: field.placeholder,
        };
      });
      this.settings = settings;
    }
  }

  public onDeleteConfirm({confirm}): void {
    this.dialogService.showConfirmDeletionDialog()
      .onConfirm(confirm.resolve)
      .onCancel(confirm.reject);
  }

  public onNewDataConfirm({confirm, newData}): void {
    this.validator(newData)
      .then(confirm.resolve)
      .catch(err => {
        this.toastrService.danger(err || 'Data validation failed!', 'Error!');
        confirm.reject(err);
      });
  }

  public refresh() {
    this.tableVisible = false;
    setTimeout(() => this.tableVisible = true);
  }


  showCreationForm() {
    const newElement = {} as T;
    const dialogName = 'Create';
    const fieldsDefinitions = this.fieldsDefinitions.filter(fd => fd.displayOnCreationForm);
    this.dialogService.showDynamicEditorDialog({
      object: newElement,
      dialogName: dialogName,
      fieldsDefinitions: fieldsDefinitions,
      editable: true
    }).onConfirm(content => {
      this.dataSource.add(content.object).then(() => this.dataSource.refresh());
    });
  }

  showUpdateForm(elementToUpdate: T) {
    const clonedElement = deepClone(elementToUpdate);
    const dialogName = 'Update ';
    const fieldsDefinitions = this.fieldsDefinitions.filter(fd => fd.displayOnUpdateForm);
    this.dialogService.showDynamicEditorDialog({
      object: clonedElement,
      dialogName: dialogName,
      fieldsDefinitions: fieldsDefinitions,
      editable: true
    }).onConfirm(content => {
      this.dataSource.update(elementToUpdate, content.object).then(() => this.dataSource.refresh());
    });
  }


}
