import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {FieldDefinitionViewModel} from '../../../generated-api/models/field-definition-view-model';
import {AbstractControl, FormControl, FormGroup, Validators} from '@angular/forms';
import {CustomValidators, getValidator, getValidatorFor} from '../../../utils/custom-validators';

export enum FormMode {
  ADD, EDIT, VIEW
}

export enum FormLayout {
  INLINE, BLOCK, NORMAL
}

export class FormModel<T> {
  constructor(public object: T,
              public fieldsDefinitions: FieldDefinitionViewModel[],
              public  formLayout: FormLayout = FormLayout.NORMAL,
              public formMode: FormMode = FormMode.ADD,
              public printLabels: boolean = true) {
  }


}


@Component({
  selector: 'ngx-dynamic-form',
  templateUrl: './dynamic-form.component.html',
  styleUrls: ['./dynamic-form.component.scss']
})
export class DynamicFormComponent<T> implements OnInit {
  @Input() public formModel: FormModel<T>;
  form: FormGroup;

  constructor(private cdr: ChangeDetectorRef) {
  }

  ngOnInit() {
    this.form = this.generateForm();
    this.cdr.detectChanges();
  }

  public updateModel() {
    this.formModel.fieldsDefinitions.filter(e => e.editable).forEach(({parameterName}) =>
      this.formModel.object[parameterName] = this.form.get(parameterName).value
    );
  }

  generateForm(): FormGroup {
    const formGroup = new FormGroup({});
    this.formModel.fieldsDefinitions
      .map(e => ({
        parameterName: e.parameterName,
        formControl: this.createFormControl(e)
      }))
      .forEach(e => formGroup.addControl(e.parameterName, e.formControl));
    formGroup.setValidators(CustomValidators.passwordsMatchValidator);
    return formGroup;
  }

  getErrorMessage(abstractControl: AbstractControl): string {
    return abstractControl.hasError('required') ? 'You must enter a value' :
      abstractControl.hasError('email') ? 'Not a valid email' :
        abstractControl.hasError('number') ? 'Value should be numeric' :
          abstractControl.hasError('confirmPassword') ? 'Passwords are different' :
            '';
  }

  isRequired(fieldDefinition: FieldDefinitionViewModel): boolean {
    return fieldDefinition.validators.filter(e => getValidatorFor(e) === Validators.required).length > 0;
  }

  isNormalInput(fieldDefinition: FieldDefinitionViewModel): boolean {
    return !(
      this.isSelect(fieldDefinition) ||
      this.isTextArea(fieldDefinition)
    );
  }

  isSelect(fieldDefinition: FieldDefinitionViewModel): boolean {
    const matched = fieldDefinition.inputType.match('^select');
    return matched && matched.length > 0;
  }

  isTextArea(fieldDefinition: FieldDefinitionViewModel): boolean {
    return fieldDefinition.inputType === 'textArea';
  }

  isMultipleSelect(fieldDefinition: FieldDefinitionViewModel): boolean {
    return fieldDefinition.inputType === 'selectMultiple';
  }

  isEditable() {
    return this.formModel.formMode !== FormMode.VIEW;
  }

  private createFormControl(fieldDefinition: FieldDefinitionViewModel): FormControl {

    const valueFromObject = this.formModel.object[fieldDefinition.parameterName];
    const value = valueFromObject ? String(valueFromObject) : '';

    return new FormControl({
      value: value,
      disabled: !fieldDefinition.editable,
    }, getValidator(fieldDefinition.validators));
  }
}

