import { LocalDataSource } from 'ng2-smart-table';
import { Observable } from 'rxjs';
import { deepExtend } from 'ng2-smart-table/lib/helpers';

export class ServiceDataSource<T> extends LocalDataSource {

  public initialized = false;

  constructor(
    initial: Observable<T[]>,
    private adder?: (value: T) => Observable<T>,
    private updater?: (value: T) => Observable<T>,
    private remover?: (value: T) => Observable<T>,
  ) {
    super();
    if (initial) {
      initial.toPromise()
        .then(values => this.load(values.reverse()))
        .then(v => this.initialized = true);
    }
  }

  prepend(element: any): Promise<any> {
    if (!this.adder) {
      return super.prepend(element);
    }
    return this.adder(element).toPromise()
      .then(v => super.prepend(v));
  }

  append(element: any): Promise<any> {
    if (!this.adder) {
      return super.append(element);
    }
    return this.adder(element).toPromise()
      .then(v => super.append(v));
  }

  add(element: any): Promise<any> {
    if (!this.adder) {
      return super.add(element);
    }
    return this.adder(element).toPromise()
      .then(v => super.add(v));
  }

  update(element: any, values: any): Promise<any> {
    if (!this.updater) {
      return super.update(element, values);
    }

    return this.find(element)
      .then(found => deepExtend(found, values))
      .then(extended => this.updater(extended).toPromise())
      .then(saved => super.update(element, saved));
  }

  remove(element: any): Promise<any> {
    if (!this.remover) {
      return super.remove(element);
    }
    return this.remover(element).toPromise()
      .then(v => super.remove(element));
  }

  isEmpty(): boolean {
    return this.initialized && this.data && this.data.length === 0;
  }
}
