import { ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NbAuthModule, NbAuthSimpleToken, NbPasswordAuthStrategy } from '@nebular/auth';
import { NbRoleProvider, NbSecurityModule } from '@nebular/security';
import { of as observableOf } from 'rxjs';

import { throwIfAlreadyLoaded } from './module-import-guard';
import { DataModule } from './data/data.module';
import { AnalyticsService } from './utils/analytics.service';
import { environment } from '../../environments/environment';
export class NbSimpleRoleProvider extends NbRoleProvider {
  getRole() {
    // here you could provide any role based on any auth flow
    return observableOf('guest');
  }
}

export const NB_CORE_PROVIDERS = [
  ...DataModule.forRoot().providers,
  ...NbAuthModule.forRoot({

    strategies: [
      NbPasswordAuthStrategy.setup({
        name: environment.securityStrategyName,
        baseEndpoint: '/api',
        login: {
          endpoint: '/Authentication/Authenticate',
          method: 'post',
        },
        logout: {
          endpoint: '/Authentication/Logout',
          redirect: {
            success: '/',
            failure: '/',
          },
        },
        register: {
          endpoint: '/newaccount',
        },
        requestPass: {
          endpoint: '/resetpassword',
        },
        token: {
          class: NbAuthSimpleToken,
          key: 'content', // this parameter tells where to look for the token
        },
      }),
    ],
    forms: {
      login: {
        strategy: environment.securityStrategyName,
        redirectDelay: 500,
        showMessages: {     // show/not show success/error messages
          success: true,
          error: true,
        },
      },
      logout: {
        strategy: environment.securityStrategyName,
      },
      register: {
        strategy: environment.securityStrategyName,
      },
      requestPassword: {
        strategy: environment.securityStrategyName,
      },
      validation: {
        firstName: {
          required: true,
          minLength: 2,
          maxLength: 10,
        },
        lastName: {
          required: true,
          minLength: 2,
          maxLength: 20,
        },
      },
    },
  }).providers,

  NbSecurityModule.forRoot({
    accessControl: {
      guest: {
        view: '*',
      },
      user: {
        parent: 'guest',
        create: '*',
        edit: '*',
        remove: '*',
      },
    },
  }).providers,

  {
    provide: NbRoleProvider, useClass: NbSimpleRoleProvider,
  },
  AnalyticsService,
];

@NgModule({
  imports: [
    CommonModule,
  ],
  exports: [
    NbAuthModule,
  ],
  declarations: [],
})
export class CoreModule {
  static forRoot(): ModuleWithProviders {
    return <ModuleWithProviders>{
      ngModule: CoreModule,
      providers: [
        ...NB_CORE_PROVIDERS,
      ],
    };
  }

  constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
    throwIfAlreadyLoaded(parentModule, 'CoreModule');
  }

}
