import { ErrorHandler, Injectable } from '@angular/core';
import { NbToastrService } from '@nebular/theme';

@Injectable()
export class HttpErrorHandler implements ErrorHandler {
  constructor(private readonly toastrService: NbToastrService) {
  }

  handleError(error: any) {
    console.error(error);
    if (error.status) {
      this.showToast(error);
    } else if (error.rejection) {
      this.showToast(error.rejection);
    } else {
      throw error;
    }
  }

  private showToast({ error: { message = '' } = {}, name = '', status = '', statusText = '' }) {
    this.toastrService.danger(message, `${name} ${status} ${statusText}`, { destroyByClick: true, duration: 2000 });
  }
}
